

.tile {
    position: relative;
    display: inline-block;
    width: 250px;
    height: 350px;
    margin-right: 10px;
    font-size: 20px;
    /* cursor: pointer; */
    transition: 450ms all;
    transform-origin: center left;
    color: white
  }
  .tile__img {
    width: 250px;
    height: 350px;
    -o-object-fit: cover;
       object-fit: cover;
  }
  .tile__details {
    position: absolute;
    bottom: 0px;
    left: 0px;
    right: 0;
    top: 0;
    font-size: 16px;
    opacity: 0;
    background: linear-gradient(180deg, rgba(0,0,0,1) 0%, rgba(255,255,255,0) 15%, rgba(255,255,255,0) 85%, rgba(0,0,0,1) 100%);
    /* linear-gradient(to top, rgba(0,0,0,0.9) 0%, rgba(0,0,0,0) 100%); */
    transition: 450ms opacity;
  }
  .tile__details:after,
  .tile__details:before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    display: #000;
  }
  
  .tile__details:before {
    position: absolute;
    left: 20px;
    width: 100%;
    font-size: 30px;
    margin-left: 100px;
    margin-top: 100px;
    bottom: 20px;
    text-align: center;
    z-index: 2;
  }
  .tile:hover .tile__details {
    opacity: 1;
  }
  .tile__title {
    position: absolute;
    bottom: 0;
    padding: 10px;
  }
  .tile__more {
    position: absolute;
    height: 1em;
    width: 1em;
    left: 0;
    top: 5px;
    padding: 10px;
    text-align: right;
    background:white;
    border-radius:1em;
    font-size:  1em;
  }
  .tile__remove {
    position: absolute;
    height: 1em;
    width: 1em;
    right: 0.5em;
    top: 5px;
    padding: 10px;
    text-align: right;
    background:white;
    border-radius:1em;
    font-size:  1em;
  }
  .tile__watched {
    position: absolute;
    height: 1em;
    width: 1em;
    right: 2em;
    top: 5px;
    padding: 10px;
    text-align: right;
    background:white;
    border-radius:1em;
    font-size:  1em;
  }
  .tile__button {
    position: absolute;
    /* text-align: center; */
    right: 0.2em;
    top: 0.1em;
    padding: 0px;
    text-align: right;
    font-size:  1em;
    color: #000;
  }
  .tile:hover ~ .tile {
    transform: translate3d(125px, 0, 0);
  }

  /* Media queries */
@media screen and (max-width:750px){

 
  
    .tile {
      position: relative;
      display: inline-block;
      width: 150px;
      height: 210px;
      margin-right: 10px;
      font-size: 20px;
      /* cursor: pointer; */
      transition: 450ms all;
      transform-origin: center left;
      color: white
    }
    .tile__img {
      width: 150px;
      height: 210px;
      -o-object-fit: cover;
         object-fit: cover;
    }
  

    
  }